    grid-template-rows: repeat(1, minmax(0, 1fr));
  }

  .lg\:border-4 {
    border-width: 4px;
  }

  .lg\:border-t-4 {
    border-top-width: 4px;
  }

  .lg\:text-4xl {
    font-size: 2.25rem;
    line-height: 2.5rem;
  }

  .lg\:text-base {
    font-size: 1rem;
    line-height: 1.5rem;
  }
}
@media (min-width: 1536px) {

  .\32xl\:w-\[30\%\] {
    width: 30%;
  }
}